      <b-spinner style="margin-top: 10px; width: 3rem; height: 3rem" variant="primary" />
    </div>

    <!-- content -->
    <div v-if="!loading" style="margin-bottom: 200px">
      <b-card class="debts-card cursor-pointer" v-for="(item, index) in prepaid" :key="index" @click="item.open = !item.open">
        <div class="d-flex align-items-center justify-content-between mb-1">
          <div class="d-flex align-items-center">
            <b-avatar class="mr-1" size="35" style="background: #e0f9fc">
              <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
