              </svg>
            </b-avatar>
            <h5 class="mb-0">{{ item.full_name }}</h5>
          </div>
          <div>
            <span class="h4">{{ item.credit }}</span>
          </div>
        </div>

        <!-- dropdown items -->
        <div class="dropdown">
          <!-- chevron-down-icon -->
          <div class="d-flex justify-content-end cursor-pointer">
            <img :src="require('@/assets/icons/chevron-down-icon.svg')" alt="" draggable="false" width="15" :class="{ 'rotate-up': item.open }" />
          </div>

          <div class="dropdown-items border-top d-flex align-items-center justify-content-between pt-1 mt-1" v-if="item.open">
            <!-- right side -->
            <div class="d-flex align-items-ceter justify-content-start cursor-pointer" @click="goDebtSettlement(item)">
              <img :src="require('@/assets/icons/calendar-icon.svg')" alt="bill-debt" draggable="false" width="18" />
              <span class="text">تسویه بدهی</span>
            </div>

            <!-- left side -->
            <div class="d-flex align-items-ceter justify-content-start cursor-pointer" @click="goRepay(item)">
              <img :src="require('@/assets/icons/calendar-icon.svg')" alt="bill-debt" draggable="false" width="18" />
              <span class="text"> عودت وجه</span>
            </div>
          </div>
        </div>
      </b-card>

      <b-card v-if="!prepaid">
        <h5 class="mb-0 text-center">هیچ اطلاعاتی جهت نمایش وجود ندارد</h5>
      </b-card>
    </div>
  </section>
