<script>
import { BCard, BAvatar, BSpinner } from 'bootstrap-vue'
import { getCredit } from '/src/api/costsServices'
// import { generateBgForBox } from "/src/@core/utils"

export default {
  name: "Prepaid",
  data() {
    return {
      loading: false,
      prepaid: [],
    }
  },
  components: {
    BCard,
    BAvatar,
    BSpinner
  },
  async mounted() {

    this.loading = true
    try {
      const { data } = await getCredit()
      this.prepaid = data.debtor_list.list
      this.loading = false
    } catch (error) {
      console.error(error)
    }

    //** add boolean variable */
    let myVar = []
    this.prepaid.map((item) => {
      myVar.push({
        'color': item.color,
        'credit': item.credit,
        'full_name': item.full_name,
        'icon': item.icon,
        'user_id': item.user_id,
        'open': false
      })
    })
    this.prepaid = myVar
  },
  methods: {
    //**Debt Settlement */
    goDebtSettlement(item) {
      this.$store.commit("costs/setGetUserPrepaidDetails", item)
      this.$router.push({ name: 'debt-settlement', params: { id: item.user_id } })
    },

    //**Repay */
    goRepay(item) {
      this.$store.commit("costs/setGetUserPrepaidDetails", item)
      this.$router.push({ name: 'repay', params: { id: item.user_id } })
    },
  },
}
</script>

