import Api from '@/api/api'

/**-----------  Start Prepaid -----------  */
function getCredit(data) {
    return Api().get('manager/credit', data)
}
function getUserDebt(data) {
    return Api().get('manager/user-debt/user-unit-debt', data)
}
function submitCreditCheckout(data) {
    return Api().post('manager/credit/checkout', data)
}
function creditRefund(data) {
    return Api().post('manager/credit/refund', data)
}
/**-----------  End Prepaid -----------  */

/**-----------  Start Other -----------  */
function getDebtCategory(data) {
    return Api().get('manager/debt-category', data)
}
function storeDebtCategory(data) {
    return Api().post('manager/debt-category/store', data)
}
/**-----------  End Other -----------  */

/**-----------  Start page online-payments -----------  */
function getOnlineIncome(data) {
    return Api().get('manager/online-income', data)
}
function getOnlineIncomeDetail(data) {
    return Api().get('manager/online-income/detail', data)
}
function verifyOnlineIncome(data) {
    return Api().post('manager/online-income/verify', data)
}
function getExpenseAvailable(data) {
    return Api().get('manager/expense/expense-name', data)
}
function expenseCategoryDelete(data) {
    return Api().delete('manager/expense/expense-category-delete?expense_category_id=' + data.expense_category_id, data)
}
function expenseCategoryStore(data) {
    return Api().post('manager/expense/expense-category-store', data)
}
function getCommonDebtCategory(data) {
    return Api().get('manager/common/debt-category', data)
}
/**-----------  End page online-payments -----------  */

/** ----------------Start page prepaid---------------- */
function getReceiptType(data) {
    return Api().get('manager/common/receipt-type', data)
}
/** ----------------End page prepaid ----------------*/


export {
    getCredit,
    creditRefund,
    getUserDebt,
    submitCreditCheckout,
    getExpenseAvailable,
    getDebtCategory,
    storeDebtCategory,
    getCommonDebtCategory,
    getOnlineIncome,
    getOnlineIncomeDetail,
    expenseCategoryDelete,
    expenseCategoryStore,
    verifyOnlineIncome,
    getReceiptType
}